.navStyle {
  display: flex;
  justify-content: center !important;
}

.container.accordion_style {
  padding-top: 50px;
}
.card.card_style {
  margin-bottom: 17px;
}
h5.mb-0.title_style {
  display: flex;
  justify-content: space-between;
  color: #121619;
}

.description_book {
  padding-top: 15px;
  border-top: 1px solid lightgray;
}
.row.row_info {
  padding-bottom: 15px;
}
a.btn.btn-default.btn_custom {
  margin-left: 0px;
  background: rgb(80, 79, 79) !important;
}

.accordion > .card .card-header {
  background: rgba(187, 222, 251, 1) !important;
}

.searchClass {
  position: relative;
  width: 80%;
  margin: 0 auto;
  display: flex;
  justify-content: center;
  padding-top: 50px;
}

.search__input {
  width: 60%;
  padding: 12px 24px;

  background-color: transparent;
  transition: transform 250ms ease-in-out;
  font-size: 18px;
  line-height: 18px;

  color: gray;
  background-color: transparent;
  background-image: url("data:image/svg+xml;charset=utf8,%3Csvg xmlns='http://www.w3.org/2000/svg' width='24' height='24' viewBox='0 0 24 24'%3E%3Cpath d='M15.5 14h-.79l-.28-.27C15.41 12.59 16 11.11 16 9.5 16 5.91 13.09 3 9.5 3S3 5.91 3 9.5 5.91 16 9.5 16c1.61 0 3.09-.59 4.23-1.57l.27.28v.79l5 4.99L20.49 19l-4.99-5zm-6 0C7.01 14 5 11.99 5 9.5S7.01 5 9.5 5 14 7.01 14 9.5 11.99 14 9.5 14z'/%3E%3Cpath d='M0 0h24v24H0z' fill='none'/%3E%3C/svg%3E");
  background-repeat: no-repeat;
  background-size: 18px 18px;
  background-position: 95% center;
  border-radius: 50px;
  border: 1px solid rgba(187, 222, 247, 1);
  transition: all 250ms ease-in-out;
  backface-visibility: hidden;
  transform-style: preserve-3d;
  /* margin-bottom: 40px; */
  border-color: rgba(187, 222, 247, 1);
}

svg {
  fill: rgba(187, 222, 247, 1);
}

.search__input:hover,
.search__input:focus {
  padding: 12px 20px 12px 0;
  outline: 0;
  border: 1px solid transparent;
  border-bottom: 1px solid rgba(187, 222, 247, 1);
  border-radius: 0;
  background-position: 100% center;
}

::placeholder {
  color: lightgray;
}
